<template>
  <div id="style-operate">
    <!-- Form 图表查询条件 -->
    <el-form :inline="true" :model="fromData" class="demo-form-inline" :label-width="100" label-position="right">
      <el-form-item :label="$t('SpuChart.regionSpu.selectData')" style="width: 20%">
        <el-date-picker
            v-model="fromData.selectData"
            type="year"
            :placeholder="$t('SpuChart.regionSpu.selectDataPlaceholder')"
            format="YYYY" value-format="YYYY"
        />
      </el-form-item>
      <el-form-item :label="$t('SpuChart.regionSpu.region')">
        <el-select
            v-model="fromData.redionList"
            multiple
            filterable
            collapse-tags
            style="margin-left: 20px;"
            :placeholder="$t('SpuChart.regionSpu.regionPlaceholder')"
        >
          <el-option v-for="item in fromData.regionList" :key="item.id" :label="item.name" :value="item.id">
            <span style="float: left">{{ item.name }}</span>
            <span style="float: right; color: var(--el-text-color-secondary); font-size: 13px;">{{ item.code }}</span>
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="listData()" icon="Search"> {{ $t('common.button.search') }} </el-button>
        <el-button type="info" @click="reset()" icon="Loading">  {{ $t('common.button.reset') }} </el-button>
      </el-form-item>
    </el-form>

    <EChartsView v-show="resData" ref="line" style="height: 600px"/>
    <el-empty v-if="!resData" image-size="300" style="height: 100%; width: 100%;" :description="$t('common.table.noData')"></el-empty>
  </div>
</template>

<script>
import spuSelected from "@/views/spuData/spu/spu-selected";
import EChartsView from "@/views/common/EChartsView";
import {getRegionSpuChart} from "@/http/spuOred/chartApi";
import {getNewYStr} from "@/util/DateUtil";
import * as echarts from "echarts";
import {getRegionPullDown} from "@/http/clienteleApi";
import {ElLoading} from "element-plus";
import {getSysTheme} from "@/util/system";

export default {
  name: "style-operate",
  components: {spuSelected, EChartsView},
  // data: 对象就是要渲染到页面上的数据
  data: function () {
    return {
      fromData: {
        selectData: getNewYStr(new Date()),
      },
      getDateStutas: false,
      option: {
        title: {
          text: ''
        },
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          // 类别数组 data: ['Email', 'Union Ads', 'Video Ads', 'Direct', 'Search Engine']
          data: []
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          // x轴数据 data: ['2022-01', '2022-02', '2022-03']
          data: []
        },
        yAxis: {
          type: 'value'
        },
        // 数据数组 series: [name: 'Email',type: 'line',stack: 'Total',data: [120, 132, 101, 134, 90, 230, 210]}]
        series: []
      },
      resData: true,
    }
  },

  // moubted: 页面加载完毕后执行
  mounted() {
    // 地区 暂测
    getRegionPullDown().then(res => {
      if (res.data.status == 200) {
        this.fromData.regionList = res.data.data;
        this.fromData.redionList = [res.data.data[0].id, res.data.data[1].id, res.data.data[2].id, res.data.data[3].id];
        this.listData();
      }
    }).catch((erro) => { });
  },

  // methods: 对象中的方法，可以在页面中调用
  methods: {
    // 避坑啊 ！！！ 组件选择的月份少一个月 而new Date()是当前月份 沉醉.........
    listData() {
      const loading = ElLoading.service({
        lock: true, text: this.$t('common.loadingText'), background: 'rgba(0, 0, 0, 0.7)',}
      );
      // this.fromData.redionList = [242, 243, 244, 245];
      getRegionSpuChart(this.fromData).then(res => {
        if (res.data.data.legendDataList.length === 0 || res.data.data.xaxisDataList.length === 0) {
          this.resData = false;
        } else {
          this.resData = true;
          // 在获取新的数据前销毁已有的图表实例
          if (this.$refs.line && this.$refs.line.chart) {
            this.$refs.line.chart.dispose();
          }
          if (getSysTheme()) {
            // 创建新的图表实例 图表背景
            this.$refs.line.chart = echarts.init(this.$refs.line.$el, 'dark');
            this.option.backgroundColor = '#181818';
          } else {
            // 创建新的图表实例 图表背景
            this.$refs.line.chart = echarts.init(this.$refs.line.$el, 'dark');
            this.option.backgroundColor = '#ffffff';
          }
          // 更新图表数据
          this.option.legend.data = res.data.data.legendDataList;
          this.option.xAxis.data = res.data.data.xaxisDataList;
          this.option.series = res.data.data.series;
          this.$nextTick(() => {
            // 使用 setOption 更新图表
            this.$refs.line.chart.setOption(this.option);
          }, 150);
        }
      }).finally(() => {
        // 请求无论成功/失败 都执行关闭loading
        loading.close();
      });
    },
    // 重置按钮
    reset() {
      this.getDateStutas = false;
      this.fromData.selectData = getNewYStr(new Date());
      this.listData();
      this.listData();
    },

    // 点击遮罩和x时执行
    beforeClose(done) {
      this.closeButton();
      done(); // 必须调用 done，以继续关闭对话框
    },
    // 取消按钮
    closeButton() {
      this.dialogFormVisible = false;
    },
  },
}
</script>

<!--组件css样式 scoped:表示该样式只在此组件生效-->
<style scoped>
.el-button--text {
  margin-right: 15px;
}

.el-select {
  width: 300px;
}

.el-input {
  width: 300px;
}

.dialog-footer button:first-child {
  margin-right: 10px;
}

.avatar-uploader .avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>

<style>
.avatar-uploader .el-upload {
  border: 1px dashed var(--el-border-color);
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: var(--el-transition-duration-fast);
}

.avatar-uploader .el-upload:hover {
  border-color: var(--el-color-primary);
}

.el-icon.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  text-align: center;
}
</style>
