import request from '../request'


// 获取产品月份客诉率图表数据
export function getMonthSpuChart(data) {
    return request({
        url: '/api/after/getMonthSpuChart',
        method: 'post',
        data
    })
}

// 获取产品地区工单图表数据
export function getRegionSpuChart(data) {
    return request({
        url: '/api/after/getRegionSpuChart',
        method: 'post',
        data
    })
}

