<template>
  <div id="style-operate">
    <!-- Form 图表查询条件 -->
    <el-form :inline="true" :model="fromData" class="demo-form-inline" :label-width="100" label-position="right">
      <el-form-item :label="$t('SpuChart.monthSpu.selectData')" style="width: 20%">
        <el-date-picker
            v-model="fromData.selectData"
            type="month"
            :placeholder="$t('SpuChart.monthSpu.selectDataPlaceholder')"
            format="YYYY-MM" value-format="YYYY-MM"
            @change="getDateStutas = true"
        />
      </el-form-item>
      <el-form-item :label="$t('SpuChart.monthSpu.number')">
        <el-select filterable v-model="fromData.number" :placeholder="$t('SpuChart.monthSpu.numberPlaceholder')" style="width: 150px;">
          <el-option v-for="item in fromData.numberList" :key="item.id" :label="item.name" :value="item.id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="listData()" icon="Search"> {{ $t('common.button.search') }} </el-button>
        <el-button type="info" @click="reset()" icon="Loading">  {{ $t('common.button.reset') }} </el-button>
      </el-form-item>
    </el-form>

    <EChartsView v-show="resData" ref="inform" style="height: 600px"/>
    <el-empty v-if="!resData" image-size="300" style="height: 100%; width: 100%;" :description="$t('common.table.noData')"></el-empty>

  </div>
</template>

<script>
import spuSelected from "@/views/spuData/spu/spu-selected";
import EChartsView from "@/views/common/EChartsView";
import {getMonthSpuChart} from "@/http/spuOred/chartApi";
import {getNewYmStr} from "@/util/DateUtil";
import {ElLoading} from "element-plus";

export default {
  name: "style-operate",
  components: {spuSelected, EChartsView},
  // data: 对象就是要渲染到页面上的数据
  data: function () {
    return {
      fromData: {
        selectData: getNewYmStr(new Date()),
        number: 10,
        numberList: [
          {id: 5, name: this.$t('SpuChart.monthSpu.numberList.five')},
          {id: 10, name: this.$t('SpuChart.monthSpu.numberList.ten')},
          {id: 15, name: this.$t('SpuChart.monthSpu.numberList.fifteen')},
          {id: 20, name: this.$t('SpuChart.monthSpu.numberList.twenty')},
        ]
      },
      getDateStutas: false,
      resData: true,
      option: {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [
          {
            type: 'category',
            data: [],
            axisTick: {
              alignWithLabel: true
            }
          }
        ],
        yAxis: [
          {
            type: 'value'
          }
        ],
        series: [
          {
            name: this.$t('SpuChart.monthSpu.afterQuantity'),
            type: 'bar',
            barWidth: '60%',
            data: []
          }
        ]
      },
    }
  },

  // moubted: 页面加载完毕后执行
  mounted() {
    this.listData();
  },

  // methods: 对象中的方法，可以在页面中调用
  methods: {
    // 避坑啊 ！！！ 组件选择的月份少一个月 而new Date()是当前月份 沉醉.........
    listData() {
      const loading = ElLoading.service({
        lock: true, text: this.$t('common.loadingText'), background: 'rgba(0, 0, 0, 0.7)',}
      );
      getMonthSpuChart(this.fromData).then(res => {
        if (res.data.data.xstrList.length === 0 || res.data.data.ydataList.length === 0) {
          this.resData = false;
        } else {
          this.resData = true;
          this.option.xAxis[0].data = res.data.data.xstrList;
          this.option.series[0].data = res.data.data.ydataList;
          this.$refs.inform.setChartsData(this.option);
        }
      }).finally(() => {
        // 请求无论成功/失败 都执行关闭loading
        loading.close();
      });
    },
    // 重置按钮
    reset() {
      this.getDateStutas = false;
      this.fromData = {
        selectData: getNewYmStr(new Date()),
        number: 10,
        numberList: [
          {id: 5, name: this.$t('SpuChart.monthSpu.numberList.five')},
          {id: 10, name: this.$t('SpuChart.monthSpu.numberList.ten')},
          {id: 15, name: this.$t('SpuChart.monthSpu.numberList.fifteen')},
          {id: 20, name: this.$t('SpuChart.monthSpu.numberList.twenty')},
        ]
      };
      this.listData();
    },

    // 点击遮罩和x时执行
    beforeClose(done) {
      this.closeButton();
      done(); // 必须调用 done，以继续关闭对话框
    },
    // 取消按钮
    closeButton() {
      this.dialogFormVisible = false;
    },
  },
}
</script>

<!--组件css样式 scoped:表示该样式只在此组件生效-->
<style scoped>
.el-button--text {
  margin-right: 15px;
}

.el-select {
  width: 300px;
}

.el-input {
  width: 300px;
}

.dialog-footer button:first-child {
  margin-right: 10px;
}

.avatar-uploader .avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>

<style>
.avatar-uploader .el-upload {
  border: 1px dashed var(--el-border-color);
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: var(--el-transition-duration-fast);
}

.avatar-uploader .el-upload:hover {
  border-color: var(--el-color-primary);
}

.el-icon.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  text-align: center;
}
</style>
