<template>
  <div id="spuChart">
    <el-row :gutter="20">
      <el-col :span="4"><div class="grid-content ep-bg-purple" />
        <el-card class="box-card" style="height: 730px; border: 1px #c0bdbd solid;" shadow="hover">
<!--          <template #header>-->
<!--            <div class="card-header">-->
<!--              <b>产品图表</b>-->
<!--              <el-button class="button" text>按钮</el-button>-->
<!--            </div>-->
<!--          </template>-->
          <div class="text item">
            <el-menu class="el-menu-vertical-demo" default-openeds="['1']"
                     @open="handleOpen" @close="handleClose" @select="handleSelect">
              <el-sub-menu index="1">
                <template #title>
                  <el-icon><location /></el-icon>
                  <span>
                    {{ $t('SpuChart.title') }}
                  </span>
                </template>
                <el-menu-item :index="char.id" v-for="(char, key, index) in chartList" :key="char.id" v-show="isButton(char.symbol)">
                  {{ char.name }}
                </el-menu-item>
              </el-sub-menu>
            </el-menu>
          </div>
<!--          <template #footer>Footer content</template>-->
        </el-card>
      </el-col>

      <el-col :span="20"><div class="grid-content ep-bg-purple" />
        <el-card class="box-card" style="height: 730px; border: 1px #9f9d9d solid;" shadow="hover">
          <template #header>
            <div class="card-header">
              <span>{{ tagChart.name }}</span>
<!--              <el-button class="button" text>Operation button</el-button>-->
            </div>
          </template>
          <div class="charBox">
            <monthSpuChart v-if="tagChart.id == '1-1'" ref="monthSpuChart"/>
            <monthAfterChart v-if="tagChart.id == '1-3'" ref="monthAfterChart"/>
            <regionSpuChart v-if="tagChart.id == '1-2'" ref="regionSpuChart"/>
          </div>
<!--          <template #footer>Footer content</template>-->
        </el-card>
      </el-col>
    </el-row>

  </div>
</template>

<script>


import monthSpuChart from "@/views/spuOred/spuChart/monthSpuChart";
import regionSpuChart from "@/views/spuOred/spuChart/regionSpuChart";
import {resourceIsUser} from "@/util/resource";
import monthAfterChart from "@/views/spuOred/spuChart/monthAfterChart";

export default {
  name: "spuChart",
  components: {monthSpuChart, regionSpuChart, monthAfterChart},
  // data: 对象就是要渲染到页面上的数据
  data: function () {
    return {
      // 左侧菜单集合渲染
      chartList: [
        {id: '1-1', name: this.$t('SpuChart.monthSpu.name'), symbol: 'spuChart.comPlaintSelect'},
        {id: '1-3', name: this.$t('SpuChart.monthAfter.name'), symbol: 'spuChart.monthAfterChart'},
        {id: '1-2', name: this.$t('SpuChart.regionSpu.name'), symbol: 'spuChart.regionSpuChart'},
      ],
      // 用户切换右侧图表 默认是: 1-1
      tagChart: {
        id: '1-1',
        name: this.$t('SpuChart.monthSpu.name')
      },

    }
  },

  // moubted: 页面加载完毕后执行
  mounted() {

  },

  // methods: 对象中的方法，可以在页面中调用
  methods: {
    // 获取按钮权限
    isButton(symbolStr) {
      return resourceIsUser(symbolStr);
    },
    // 菜单激活回调  handleSelect(选择的index值, 数组indexPath选择的层级)
    handleSelect(index, indexPath) {
      this.chartList.forEach((item, key) => {
        if (item.id == index) {
          this.tagChart = item;
        }
      })
    },
    // sub-menu 展开的回调
    handleOpen() {
      console.log(key, keyPath)
    },
    // sub-menu 收起的回调
    handleClose() {
      console.log(key, keyPath)
    }
  },
}
</script>

<!--组件css样式 scoped:表示该样式只在此组件生效-->
<style scoped>
/* 卡片样式 */
.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.text {
  font-size: 14px;
}
.item {
  margin-bottom: 18px;
}
.box-card {
  /*width: 480px;*/
}
/deep/ .el-card__body {
  width: 100%;
  height: 100%;
  padding: 10px 0 0 0;
}

.charBox {
  width: 100%;
  overflow: auto;
  height: 700px;
  /*border: 1px #c92525 solid;*/
}

</style>
